import React, { FC } from 'react';
import {Error} from 'react-hook-form'
import { useState } from 'react';
import './input.css';


type AllProps = Condition & InputProps;


type Condition = {
    disabled?: boolean, 
    error?: boolean,
    success?: boolean,
}

type InputProps = {
    id: string,
    label: string,
    value?: string,
    placeholder: string,
    onChange: (str: string) => void,
    size?: 'large' | 'medium' | 'small';
}

export const Input = ({
    id,
    label = '',
    placeholder = '',
    disabled = false,
    error,
    onChange,
    value,
    size = 'medium',
    ...props
}: AllProps, ) => {

    const hasError = error?.message?.length


    return (
        <>
            <label className='input--label'>

                {label}
            </label>
            <input
                id={id}
                className={`input--${size}` }
                disabled={disabled}
                error={hasError}
                onChange={e => onChange(e.target.value)}
                value={value}
                placeholder={placeholder}
                {...props} />
                {hasError ? <ErrorView >{error?.message}</ErrorView> : null}
        </>
    )
}

export default Input